<template>
  <!--  -->
  <span v-if="isArray" class="json-text-inline-block">
    <span>[</span>
    <span v-for="(v, i) in value" :key="i">
      <JSONTextInlineBlock
        :name="String(i)"
        :value="v"
        :deepth="deepth + 1"
      />
      <span v-if="i !== value.length - 1">,&nbsp;</span>
    </span>
    <span>]</span>
  </span>
  <!--  -->
  <span v-else-if="isObject" class="json-text-inline-block">
    <!--  -->
    <span>{</span>
    <!-- 1 5  5  -->
    <template v-if="deepth === 0">
      <span v-for="(propName, index) in displayPropertyNames" :key="index" v-if="index < maxDisplayPropertyCount">
        <span>{{propName}}: </span>
        <JSONTextInlineBlock
          :name="propName"
          :value="value[propName]"
          :deepth="deepth + 1"
        />
        <span v-if="index !== Math.min(maxDisplayPropertyCount - 1, displayPropertyNames.length - 1)">, </span>
      </span>
      <span v-if="displayPropertyNames.length >= 5">, ...</span>
    </template>
    <!-- 1 -->
    <span v-else>...</span>
    <span>}</span>
  </span>
  <!--  -->
  <span v-else-if="isString" class="json-text-inline-block">
    <template v-if="name">
      <span class="string-quote">"</span>
      <span :class="{'string': isRoot}">{{value}}</span>
      <span class="string-quote">"</span>
    </template>
    <template v-else>
      <span>{{value}}</span>
    </template>
  </span>
  <!--  -->
  <span v-else class="json-text-inline-block" :class="isRoot ? valueClass : ''">{{formattedValue}}</span>
</template>

<script>
/**
 *  JSON 
 */
import { isNull, isBoolean, isString, isNumber, isObject, isSymbol, isUndefined, isArray } from "@/utils";

export default {
  name: "JSONTextInlineBlock",
  props: {
    // 
    // 
    // 
    name: [String, Symbol],
    // 
    value: {},
    // 
    // let obj = {a: {b: 2}}
    // obj  deepth  0
    // obj.a  deepth  1
    // obj.a.b  deepth  2 
    deepth: {
      type: Number,
      default: 0
    }
  },
  computed: {
    isRoot() {
      return this.deepth === 0;
    },
    isArray() {
      return isArray(this.value);
    },
    isString() {
      return isString(this.value);
    },
    isObject() {
      return isObject(this.value);
    },
    maxDisplayPropertyCount() {
      return 5;
    },
    displayPropertyNames() {
      if (isObject(this.value)) {
        return Object.keys(this.value);
      } else {
        return [];
      }
    },
    formattedValue() {
      const value = this.value;
      if (isNull(value) || isUndefined(value)) {
        return String(value);
      }
      return value;
    },
    valueClass() {
      const value = this.value;
      return {
        null: isNull(value),
        undefined: isUndefined(value),
        boolean: isBoolean(value),
        number: isNumber(value),
        // key
        string: isString(value),
        symbol: isSymbol(value)
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.json-text-inline-block {
  word-break: break-all;

  .normal {
    color: #565656;
  }
  .null,
  .undefined {
    color: rgb(128, 128, 128);
  }
  .boolean,
  .function {
    color: rgb(13, 34, 170);
  }
  .number {
    color: #1c00cf;
  }
  .string,
  .symbol {
    color: rgb(196, 26, 22);
  }
  .string-quote {
    color: #222;
  }
}
</style>
